    </section>


    <!-- edit -->
    <section class="overlaysEditForm animate__animated animate__fadeIn" v-if="editUser.edit === 'true'">
        <div class="formBlockEdit" >
            <div class="row">
              <div class="col-md-6">
                <h4>Edit form</h4>
              </div>                
              <div class="col-md-6 blockInputEdit d-flex justify-content-end">
