              </div>
              <div class="col-md-6">
                <div class="col-md-12 blockInputEdit">
                    <div>Driver: {{editUser.driver}}</div>
                </div>

              </div>

              <div class="col-md-12 row">
                <div class="col-md-6">
                  <div class="col-md-12 blockInputEdit">
                    <form>
                      <div class="blockInputTitleEditForm">GTR Transport Inc. </div>
                      <div class="blockInputRadio">
                        <input type="radio" name="editUser.insurance1" v-model="editUser.insurance1" value="Yes"> Yes
                      </div>
                      <button v-if="editUser.insurance1" class="btnSubmitInputEditForm animate__animated animate__fadeInDown" @click.prevent="setInsurance1(editUser._id, editUser.insurance1)"><font-awesome-icon icon="check" /></button>                    
                      <div v-if="editUser.insurance1" class="btnCearInput animate__animated animate__fadeInDown" @click.prevent="clearInsurance1(editUser._id)">X</div> 
                    </form>
                  </div>
                  <div class="col-md-12 blockInputEdit">
                    <form>
                      <div class="blockInputTitleEditForm">Alacrity Transport Inc. </div>
                      <div class="blockInputRadio">
                        <input type="radio" name="editUser.insurance2" v-model="editUser.insurance2" value="Yes"> Yes
                      </div>
                      <button v-if="editUser.insurance2" class="btnSubmitInputEditForm animate__animated animate__fadeInDown" @click.prevent="setInsurance2(editUser._id, editUser.insurance2)"><font-awesome-icon icon="check" /></button>
                      <div v-if="editUser.insurance2" class="btnCearInput animate__animated animate__fadeInDown" @click.prevent="clearInsurance2(editUser._id)">X</div> 
                    </form>
                  </div>
                  <div class="col-md-12 blockInputEdit">
                    <form>
                      <div class="blockInputTitleEditForm">Alpha Freight Inc. </div>
                      <div class="blockInputRadio">
                        <input type="radio" name="editUser.insurance3" v-model="editUser.insurance3" value="Yes"> Yes
                      </div>
                      <button v-if="editUser.insurance3" class="btnSubmitInputEditForm animate__animated animate__fadeInDown" @click.prevent="setInsurance3(editUser._id, editUser.insurance3)"><font-awesome-icon icon="check" /></button>
                      <div v-if="editUser.insurance3" class="btnCearInput animate__animated animate__fadeInDown" @click.prevent="clearInsurance3(editUser._id)">X</div> 
                    </form>
                  </div>
                  <div class="col-md-12 blockInputEdit">
                    <form>
                      <div class="blockInputTitleEditForm">Gambrinus Logistics Inc.</div>
                      <div class="blockInputRadio">
                        <input type="radio" name="editUser.insurance4" v-model="editUser.insurance4" value="Yes"> Yes
                      </div>
                      <button v-if="editUser.insurance4" class="btnSubmitInputEditForm animate__animated animate__fadeInDown" @click.prevent="setInsurance4(editUser._id, editUser.insurance4)"><font-awesome-icon icon="check" /></button>
                      <div v-if="editUser.insurance4" class="btnCearInput animate__animated animate__fadeInDown" @click.prevent="clearInsurance4(editUser._id)">X</div> 
                    </form>
                  </div>

                </div>

                <div class="col-md-6">

                  <div class="col-md-12 blockInputEdit">
                    <form>
                      <div class="blockInputTitleEditForm">JR Freight INC Professional Corporation </div>
                      <div class="blockInputRadio">
                        <input type="radio" name="editUser.insurance5" v-model="editUser.insurance5" value="Yes"> Yes
                      </div>
                    
                      <button v-if="editUser.insurance5" class="btnSubmitInputEditForm animate__animated animate__fadeInDown" @click.prevent="setInsurance5(editUser._id, editUser.insurance5)"><font-awesome-icon icon="check" /></button>
                      <div v-if="editUser.insurance5" class="btnCearInput animate__animated animate__fadeInDown" @click.prevent="clearInsurance5(editUser._id)">X</div> 
                    </form>
                  </div>


                  <div class="col-md-12 blockInputEdit">
                    <form>
                      <div class="blockInputTitleEditForm">I Express Freight Inc. </div>
                      <div class="blockInputRadio">
                        <input type="radio" name="editUser.insurance6" v-model="editUser.insurance6" value="Yes"> Yes
                      </div>
                      <button v-if="editUser.insurance6" class="btnSubmitInputEditForm animate__animated animate__fadeInDown" @click.prevent="setInsurance6(editUser._id, editUser.insurance6)"><font-awesome-icon icon="check" /></button>
                      <div v-if="editUser.insurance6" class="btnCearInput animate__animated animate__fadeInDown" @click.prevent="clearInsurance6(editUser._id)">X</div> 
                    </form>
                  </div>


                  <div class="col-md-12 blockInputEdit">
                    <form>
                      <div class="blockInputTitleEditForm">Skillman Industries Inc. </div>
                      <div class="blockInputRadio">
                        <input type="radio" name="editUser.insurance7" v-model="editUser.insurance7" value="Yes"> Yes
                      </div>
                      <button v-if="editUser.insurance7" class="btnSubmitInputEditForm animate__animated animate__fadeInDown" @click.prevent="setInsurance7(editUser._id, editUser.insurance7)"><font-awesome-icon icon="check" /></button>
                      <div v-if="editUser.insurance7" class="btnCearInput animate__animated animate__fadeInDown" @click.prevent="clearInsurance7(editUser._id)">X</div> 
                    </form>
                  </div>
                  <div class="col-md-12">
                    <div>Delete this insurance record</div>
                    <button class="btnDeleteSaferyRecord" @click="foundInsuranceDelete(editUser._id) || closeEditForm()">Delete</button>
                  </div> 
                </div>
              </div>                 
            </div>
        </div>
    </section>







    <!-- list -->
    <div>
        <div>
            <table role="table" style="width: 100%;" class="table table-bordered table-hover">
              <thead role="rowgroup">
                <tr role="row" class="text-center">
                    <th role="columnheader">#</th>
                    <th role="columnheader">Driver</th>
                    <th role="columnheader">GTR Transport Inc.</th>
                    <th role="columnheader">Alacrity Transport Inc.</th>
                    <th role="columnheader">Alpha Freight Inc.</th>
                    <th role="columnheader">Gambrinus Logistics Inc.</th>
                    <th role="columnheader">JR Freight INC Professional Corporation</th>
                    <th role="columnheader">I Express Freight Inc.</th>
                    <th role="columnheader">Skillman Industries Inc.</th>
                    <th role="columnheader">Edit</th>
                </tr>
              </thead>
              <tbody role="rowgroup">
                <tr role="row" v-for="list in pageOfItems" :key="list._id" class="text-center animate__animated animate__fadeIn">
                    <td role="cell">
                      {{list.number}}
                    </td>
                    <td role="cell">
                      <router-link title="Open profile" style="text-decoration: none; color: inherit;" :to="'user/' + list.user">{{list.driver}} <font-awesome-icon style="color: #8FBC8F; font-size: 18px;" icon="caret-right" /> 
                      </router-link>
                    </td>
                    <td role="cell">
                      {{list.insurance1}}
                    </td>
                    <td role="cell">
                      {{list.insurance2}}
                    </td>
                    <td role="cell">
                      {{list.insurance3}}
                    </td>
                    <td role="cell">
                      {{list.insurance4}}
                    </td>
                    <td role="cell">
                      {{list.insurance5}}
                    </td>
                    <td role="cell">
                      {{list.insurance6}}
                    </td>
                    <td role="cell">
                      {{list.insurance7}}
                    </td>
                    <td role="cell">
                        <button title="Edit this insurance record" class="btnEdit" @click="editUserForm(list) || toEdit(list._id) || closeFormAddInsurance()"><font-awesome-icon icon="pen" /></button>
                    </td>
                </tr>
              </tbody>
            </table>
            <div class="col-md-12 paginationUser">                   
                <jw-pagination :items="filteredListSearch" :pageSize="6" @changePage="onChangePage"></jw-pagination>
            </div>
<!--                   <div v-else style="font-size: 30px;">
                    List empty
                  </div> -->
        </div>
    </div>




  </div>
</template>

<script>
const url = 'https://testrole.herokuapp.com/';
// 'http://localhost:3000/'
// 'http://62.113.98.37:3000/'
import Vue from 'vue';
import axios from 'axios';
import authHeader from '../services/auth-header';

import JsonExcel from 'vue-json-excel'

// import _ from 'lodash';

// import moment from 'moment';


import Toasted from 'vue-toasted';
Vue.config.productionTip = false;
Vue.config.devtools = false;
Vue.use(Toasted);


export default {
  name: 'insurance_endorsement',
  components:{
    JsonExcel
  },
  data() {
    return {
      fileName:"Insurance Endorsement.xls",
      json_fields: {
        "Driver": 'driver',
        "GTR Transport Inc.": 'insurance1',
        "Alacrity Transport Inc.": 'insurance2',
        "Alpha Freight Inc.": 'insurance3',
        "Gambrinus Logistics Inc.": 'insurance4',
        "JR Freight INC Professional Corporation": 'insurance5',
        "I Express Freight Inc.": 'insurance6',
        "Skillman Industries Inc.": 'insurance7'
      },
      json_meta: [
        [
          {
            'key': 'charset',
            'value': 'utf-8'
          }
        ]
      ],
      content: [],
      contentInsurance: [],
      found: '',
      search: '',
      searchUser: '',
      empty: '',
      oldestFirst: true,
      //model 
      driver: '',
      insurance1: '',
      insurance2: '',
      insurance3: '',
      insurance4: '',
      insurance5: '',
      insurance6: '',
      insurance7: '',
      //
      showModal: false,
      showForm: false,
      showModalMenu: false,
      editUser: [],
      pageOfItems: [],
      validateUser: false,
    };
  },
  mounted() {
    // all data
    this.getData();
    this.getDataInsurance();
  },
  computed: {
    // dub(){
    //     const found = this.contentInsurance.find(element => element._id === this.found.user);
    //     if(found){
    //       console.log(found)
    //     }
    //     return found
    // },
    all() {
      var vm = this
      var n = 1
      var edit = "false"
      var result = vm.sortedByDate.map(function(el) {
        var o = Object.assign({}, el);
        o.number = n++;
        o.edit = edit;
        return o;
      })
      return result
    },
    sortedByDate() {
      var order = this.oldestFirst ? 1 : -1;   
       return [...this.contentInsurance].sort(function(a, b) {
        a = new Date(a.date);
        b = new Date(b.date);
        var results = a > b ? -1 : a < b ? 1 : 0;
        return results * order;
      });
    },
    filteredList() {
      return this.content.filter(list => {
        return list.firstName.toLowerCase().includes(this.search.toLowerCase()) + list.lastName.toLowerCase().includes(this.search.toLowerCase())
      })
    },
    filteredListSearch() {
      return this.all.filter(list => {
        return list.driver.toLowerCase().includes(this.searchUser.toLowerCase())
      })
    }
  },
  methods: {
    editUserForm(user){
      this.editUser = user
    },
    toEdit(id) {
      var el = this.all.find(item => item._id === id);
      if (el) {
        el.edit = "true";
      }
    },
    getData() {
      axios
        .get(url + 'admin/', { headers: authHeader()})
          .then(response => {
            return response.data
          })
          .then(data => {
            this.content = data;
          })
          .catch(err => {
             console.log(err)
          })
    },
    getDataInsurance() {
      axios
        .get(url + 'admin/insurance', { headers: authHeader()})
          .then(response => {
            return response.data
          })
          .then(data => {
            this.contentInsurance = data;
          })
          .catch(err => {
             console.log(err)
          })
    },
    toggleSort: function() {
        this.oldestFirst = !this.oldestFirst;
        if(this.oldestFirst){
          Vue.toasted.show('First new records', {
            theme: "toasted-primary",
            position: "top-right",
            duration: 3000,
          });
        }
        else{
          Vue.toasted.show('First old records', {
            theme: "toasted-primary",
            position: "top-right",
            duration: 3000,
          });

        }
    },
    find(name){
        const foundUser = this.content.find(element => element.firstName === name );
        this.found = foundUser;
        if(foundUser){
          const foundDub = this.contentInsurance.find(element => element.user === this.found._id);
          if(foundDub){
            this.validateUser = true;
          }
        }
        this.search = this.empty;
    },
    clearFound(){
        this.validateUser = false;
        this.found = this.empty
    },
    setForm(id) {
            const updateData = {
              driver: this.found.firstName + ' ' + this.found.lastName, 
              insurance1: this.insurance1,
              insurance2: this.insurance2,
              insurance3: this.insurance3,
              insurance4: this.insurance4,
              insurance5: this.insurance5,
              insurance6: this.insurance6,
              insurance7: this.insurance7
            }
            axios
              .post(url + 'user/insurance/' + id + '/insurance', updateData, { headers: authHeader()})
              .then(() => {
                this.getDataInsurance();
                this.driver = this.empty
                this.insurance1 = this.empty
                this.insurance2 = this.empty
                this.insurance3 = this.empty
                this.insurance4 = this.empty
                this.insurance5 = this.empty
                this.insurance6 = this.empty
                this.insurance7 = this.empty
                this.found = this.empty
                Vue.toasted.show('Insurance record added &#9989;', {
                  theme: "toasted-primary",
                  position: "top-right",
                  duration: 3000,
                });
             });
      },
      setInsurance1(id, text) {
        if(text){
            const updateData = { insurance1: text }
            axios
              .put(url + 'user/insurance/' + id, updateData, { headers: authHeader()})
              .then(() => {
                Vue.toasted.show('&#9989;', {
                  theme: "toasted-primary",
                  position: "top-right",
                  duration: 3000,
                });
             });
        }
        else {
            Vue.toasted.show('&#10060;', {
                theme: "toasted-primary",
                position: "top-right",
              duration: 3000,
            });
        }

      },
      clearInsurance1(id) {
        const updateData = { insurance1: this.empty }
        axios
          .put(url + 'user/insurance/' + id, updateData, { headers: authHeader()})
          .then(() => {
            this.editUser.insurance1 = this.empty;
            Vue.toasted.show('&#9989;', {
              theme: "toasted-primary",
              position: "top-right",
              duration: 3000,
            });
          });
      },


      setInsurance2(id, text) {
        if(text){
            const updateData = { insurance2: text }
            axios
              .put(url + 'user/insurance/' + id, updateData, { headers: authHeader()})
              .then(() => {
                Vue.toasted.show('&#9989;', {
                  theme: "toasted-primary",
                  position: "top-right",
                  duration: 3000,
                });
             });
        }
        else {
            Vue.toasted.show('&#10060;', {
                theme: "toasted-primary",
                position: "top-right",
              duration: 3000,
            });
        }
      },
      clearInsurance2(id) {
        const updateData = { insurance2: this.empty }
        axios
          .put(url + 'user/insurance/' + id, updateData, { headers: authHeader()})
          .then(() => {
            this.editUser.insurance2 = this.empty;
            Vue.toasted.show('&#9989;', {
              theme: "toasted-primary",
              position: "top-right",
              duration: 3000,
            });
          });
      },




      setInsurance3(id, text) {
        if(text){
            const updateData = { insurance3: text }
            axios
              .put(url + 'user/insurance/' + id, updateData, { headers: authHeader()})
              .then(() => {
                Vue.toasted.show('&#9989;', {
                  theme: "toasted-primary",
                  position: "top-right",
                  duration: 3000,
                });
             });
        }
        else {
            Vue.toasted.show('&#10060;', {
                theme: "toasted-primary",
                position: "top-right",
              duration: 3000,
            });
        }
      },
      clearInsurance3(id) {
